<template>
  <b-container fluid>
    <b-row>
      <b-col lg="12">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">Verify Health ID</h4>
          </template>
          <template v-slot:body>
            <p>Capture & Verify Health ID / Health ID Number.</p>
            <form @submit.prevent="onSubmit">
              <div class="form-row">
                <div class="col-md-12 mb-3">
                  <label for="healthid">Health ID</label>
                  <div class="input-group">
                    <input
                      type="text"
                      v-model.trim="$v.user.healthid.$model"
                      class="form-control"
                      :class="{
                        'is-invalid': $v.user.healthid.$error,
                        'is-valid': $v.user.healthid.$valid,
                      }"
                      placeholder="username"
                    />
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroupPrepend"
                        >@</span
                      >
                    </div>
                    <input
                      type="text"
                      v-model.trim="$v.user.cm.$model"
                      class="form-control"
                      :class="{
                        'is-invalid': $v.user.cm.$error,
                        'is-valid': $v.user.cm.$valid,
                      }"
                      placeholder="cm"
                    />
                  </div>
                </div>
              </div>
              <div class="col-md-12">
                <p class="text-center">OR</p>
              </div>
              <div class="form-group">
                <label for="healthidnumber">Health ID Number</label>
                <input
                  type="text"
                  v-model.number="$v.user.healthidnumber.$model"
                  @keypress="isNumber($event)"
                  class="form-control"
                  :class="{
                    'is-invalid': $v.user.healthidnumber.$error,
                    'is-valid': $v.user.healthidnumber.$valid,
                  }"
                  placeholder="71-6055-4664-8882"
                />
                <div class="invalid-feedback">
                  Health ID number is invalid
                </div>
              </div>
              <p>
                Enter atlest one, either Health ID or Health ID Number.
              </p>
              <div class="form-group">
                <button class="btn btn-primary" type="verify">Verify</button>
              </div>
            </form>
          </template>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { keyrchain } from "../../config/pluginInit";
import {
  required,
  minLength,
  maxLength,
  numeric,
} from "vuelidate/lib/validators";
export default {
  name: "FormValidates",
  data() {
    return {
      user: {
        healthid: "",
        healthidnumber: "",
        cm: "",
      },
      submitted: false,
    };
  },
  validations() {
    if (!this.user.healthid) {
      return {
        user: {
          healthid: { minLength: minLength(2), maxLength: maxLength(30) },
          cm: { minLength: minLength(2), maxLength: maxLength(10) },
          healthidnumber: { required, numeric, minLength: minLength(14), maxLength: maxLength(14)},
        },
      };
    } else {
      return {
        user: {
          healthid: { required, minLength: minLength(2), maxLength: maxLength(30),},
          cm: { required, minLength: minLength(2), maxLength: maxLength(10) },
          healthidnumber: { numeric, minLength: minLength(14), maxLength: maxLength(14)},
        },
      };
    }
  },
  mounted() {
    keyrchain.index();
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.user.healthid = "";
      this.user.cm = "";
      this.user.healthidnumber = "";
    },
    isNumber: function (evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
  },
};
</script>
