<template>
  <b-container fluid>
    <b-row>
      <b-col lg="12">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">Search / Issue Health ID</h4>
          </template>
          <template v-slot:body>
            <p>
              Help patient in creating NDHM Health ID. Get patient consent
              before proceeding. Choose either Aadhaar or Mobile linked Health
              ID.
            </p>
            <form @submit.prevent="onSubmit">
              <div class="form-row">
                <div class="col-md-6">
                  <label for="patientname">Patient Name</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="$v.user.patientname.$model"
                    :class="{
                      'is-invalid': $v.user.patientname.$error,
                      'is-valid': $v.user.patientname.$valid,
                    }"
                    placeholder="FirstName  MiddleName  LastName"
                  />
                  <div class="invalid-feedback">
                    Please provide a valid Patient Name.
                  </div>
                </div>
                <div class="col-md-6">
                  <b-form>
                    <b-form-group
                      label="Month & Year of Birth"
                      label-for="Inputyear"
                    >
                      <b-form-input
                        id="Inputyear"
                        type="month"
                        class="form-control"
                        v-model="$v.user.monthyearofbirth.$model"
                        :class="{
                          'is-invalid': $v.user.monthyearofbirth.$error,
                          'is-valid': $v.user.monthyearofbirth.$valid,
                        }">
                      </b-form-input>
                      <div class="invalid-feedback">this is invalid</div>
                    </b-form-group>
                  </b-form>
                </div>
                <div class="col-md-6 mb-3">
                  <label for="gender">Gender</label>
                  <select
                    class="form-control"
                    v-model="$v.user.gender.$model"
                    :class="{
                      'is-invalid': $v.user.gender.$error,
                      'is-valid': $v.user.gender.$valid,
                    }"
                  >
                    <option selected disabled value="">Choose...</option>
                    <option value="Female">Female</option>
                    <option value="Male">Male</option>
                    <option value="Others">Others</option>
                  </select>
                  <div class="invalid-feedback">Please choose a gender.</div>
                </div>
                <div class="col-md-6 mb-3">
                  <label for="mobilenumber">Mobile Number</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="$v.user.mobilenumber.$model"
                    @keypress="mobilenumber($event)"
                    :class="{
                      'is-invalid': $v.user.mobilenumber.$error,
                      'is-valid': $v.user.mobilenumber.$valid,
                    }"
                    placeholder="9930000000"
                  />
                  <div class="invalid-feedback">
                    Please provide a valid Mobile Number.
                  </div>
                </div>
              </div>
              <button type="submit" class="btn btn-primary">Submit</button>
            </form>
          </template>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import { keyrchain } from "../../config/pluginInit";
import { required, minLength, maxLength, numeric } from "vuelidate/lib/validators";
export default {
  name: "FormValidates",
  data() {
    return {
      user: {
        patientname: "",
        mobilenumber: "",
        gender: "",
        monthyearofbirth: "",
      },
      submitted: false,
    };
  },
  validations: {
    user: {
      patientname: { required, minLength: minLength(3), maxLength: maxLength(30) },
      mobilenumber: { required, numeric, minLength: minLength(10), maxLength: maxLength(10) },
      gender: { required },
      monthyearofbirth: { required },
    },
  },
  mounted() {
    keyrchain.index();
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.user.patientname = "";
      this.user.mobilenumber = "";
      this.user.gender = "";
      this.user.monthyearofbirth = "";
    },
    mobilenumber: function (evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
  },
};
</script>
